address,
cite,
em,
img,
q,
strong,
sub,
sup {
  border: 0;
  font: inherit;
  font-size: 100%;
  margin: 0;
  padding: 0;
  -webkit-text-size-adjust: none;
  vertical-align: baseline;
}

address {
  font-weight: normal;
}

cite {
  font-size: 0.875rem;
  font-style: italic;
  font-weight: 400;
  line-height: calc(18 / 14);
}

em {
  font-style: italic;
}

img {
  font-weight: normal;
}

q {
  font-style: italic;
}

q::before {
  content: open-quote ' ';
}

q::after {
  content: ' ' close-quote;
}

strong {
  font-weight: normal;
  font-family: "FS Albert", Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-weight: 600;
}

sub {
  font-size: 0.75rem;
  line-height: 1rem;
  vertical-align: sub;
}

sup {
  font-size: 0.75rem;
  line-height: 1rem;
  vertical-align: super;
}
